/* eslint-disable no-restricted-syntax */
import createDomEl from './create'

export default {
  markup: document.querySelector('.markup__viewer'),
  items: document.querySelector('.items').children,

  renderMarkup(): void {
    this.markup.innerHTML = '';
    for (const e of this.items) {
      const elem = this.renderElement(e)
      this.markup.appendChild(elem)
    }
  },

  renderElement(e): HTMLElement {
    const wrapper = createDomEl('div', 'code');
    let template = ''
    const elemTag: string = e.tagName.toLowerCase();
    const elemClass = Array.from(e.classList).filter((elem: string) => elem !== 'open' && elem !== 'strobe');
    const elemId = e.id;
    const elemData = e.getAttribute('for');
    if (elemTag) template += `<${elemTag}`;
    if (elemClass.length > 0) template += ` class="${elemClass}"`;
    if (elemId) template += ` id="${elemId}"`;
    if (elemData) template += ` for="${elemData}"`;
    if (elemTag) template += `>`;
    wrapper.append(template);
    if (e.children.length > 0) {
      for(const elem of e.children) {
        wrapper.appendChild(this.renderElement(elem))
      }
    }
    const closeTag = `</${elemTag}>`
    if (elemTag) wrapper.append(closeTag);
    return wrapper;
  }
}
