import {levels} from './levels'
import createDomEl from './create'

export default {
  doneLevels: [],
  helpLevels: [],
  isOpen: false,
  menu: document.querySelector('.menu-level__levels'),
  init(): void {
    levels.forEach((level: any, i: number) => {
      createDomEl('a', 'menu-level__link link',
        [
          createDomEl('span', 'menu-level__check-mark check-mark', null, null),
          createDomEl('span', 'menu-level__num', `${i + 1}`, null),
          createDomEl('span', 'menu-level__selector', level.taskSelector, null),
        ],
      this.menu, )
    });
    this.links = this.menu.querySelectorAll('.menu-level__link');
    this.addDoneStyle();
  },

  setDoneLevels(num: number, help: boolean): void{
    if (help) {
      if (!this.helpLevels.includes(num)){
        this.helpLevels.push(num);
      }
      localStorage.setItem('help-levels', JSON.stringify(this.helpLevels))
    }else{
      if (!this.doneLevels.includes(num)){
        this.doneLevels.push(num);
      }
      localStorage.setItem('done-levels', JSON.stringify(this.doneLevels))
    }

  },

  getDoneLevels(): void{
    const localStorageList = JSON.parse(localStorage.getItem('done-levels'))
    const localStorageHelpList = JSON.parse(localStorage.getItem('help-levels')) 
    this.doneLevels = localStorageList ? localStorageList : [];
    this.helpLevels = localStorageHelpList ? localStorageHelpList : [];
  },

  addDoneStyle(): void{
    this.getDoneLevels();    
    this.links.forEach(e => {
      const mark: HTMLElement = e.querySelector('.menu-level__check-mark');
      const number: number = +e.querySelector('.menu-level__num').innerText; 
      if (this.helpLevels.includes(number)) {
        mark.classList.add('check-mark__helped')
      }
      if (this.doneLevels.includes(number)) {
        mark.classList.add('check-mark__completed')
      }
    });
  },

  removeDoneStyle(): void{
    this.links.forEach(e => {
      const mark: HTMLElement = e.querySelector('.menu-level__check-mark');   
      mark.classList.remove('check-mark__completed')
      mark.classList.remove('check-mark__helped')
    });
  },

  setCurrentLevel(num: number): void {
    this.menu.querySelectorAll('a').forEach((e: HTMLElement) => {
      e.classList.remove('link__current')
    })
    this.menu.querySelector(`a:nth-child(${num})`).classList.add('link__current');
  },

  resetProgress(): void{
    localStorage.removeItem('done-levels');
    localStorage.removeItem('help-levels');
    this.doneLevels = []
    this.helpLevels = []
    this.removeDoneStyle();
  },

  toggleMenu() {
    const menu = document.querySelector('.aside')
    if(this.isOpen) {
      menu.classList.remove('menu_open');
      this.isOpen = false
    } else {
      menu.classList.add('menu_open');
      this.isOpen = true
    }
  },
}