export default {
  currentLetter: 0,
  help (text: string, input: HTMLInputElement, delay: number): void {
    this.currentLetter = 0;
    this.text = text;
    this.input = input;
    this.delay = delay;
    this.write()
  },

  write(): void {
    this.input.value += this.text.substr(this.currentLetter, 1)
    this.currentLetter += 1;
    if (this.currentLetter < this.text.length) {
      setTimeout(() => this.write(), this.delay)
    }
  },
}
