//появление подсказки взято с https://learn.javascript.ru/mousemove-mouseover-mouseout-mouseenter-mouseleave

export default {
  getItems():void {
    this.items = Array.from(document.querySelectorAll('.items *'));
    this.codeItems = Array.from(document.querySelectorAll('.markup__viewer div'))
    this.helper = document.querySelector('.helper')
  },

  showTooltip(e): void {
    if(e.target.closest('.items')) {
      this.showTableTooltip(e)
      return
    }
    if(e.target.closest('.markup__viewer')) {
      this.showEditor(e)
    }
  },

  hideTooltip(e): void {
    if(e.target.closest('.items')) {
      this.hideTableTooltip(e)
      return
    }
    if(e.target.closest('.markup__viewer')) {
      this.hideEditor(e)
    }
  },

  showText(target: HTMLElement): void {
    this.helper.style.display = 'block';
    const coords = target.getBoundingClientRect();

    // позиционируем подсказку над центром элемента
    let left = coords.left + target.offsetWidth - this.helper.offsetWidth / 2;
    if (left < 0) left = 0;

    let top = coords.top - this.helper.offsetHeight - 25;
    if (top < 0) {
      top = coords.top + this.helper.offsetHeight + 25;
    }

    this.helper.style.left = `${left}px`;
    this.helper.style.top = `${top}px`;

    let template = ''
    const elemTag: string = target.tagName.toLowerCase();
    const elemClass = Array.from(target.classList).filter((elem: string) => elem !== 'open' && elem !== 'strobe');
    const elemId = target.id;
    const elemData = target.getAttribute('for');
    if (elemTag) template += `<${elemTag}`;
    if (elemClass.length > 0) template += ` class="${elemClass}"`;
    if (elemId) template += ` id="${elemId}"`;
    if (elemData) template += ` for="${elemData}"`;
    if (elemTag) template += `>`;
    if (elemTag) template += `</${elemTag}>`;
    this.helper.innerText = template
  },

  showTableTooltip(e): void {
    e.stopPropagation()
    if (e.target.classList.contains('items')) return
    const { target } = e;
    const ind = this.items.indexOf(target)
    this.items[ind].dataset.hovered = true;
    this.codeItems[ind].classList.add('hovered');
    this.showText(target);
  },

  showEditor(e): void {
    e.stopPropagation()
    if (e.target.classList.contains('markup__viewer')) return
    const ind = this.codeItems.indexOf(e.target.closest('div'))
    if (ind !== -1) {
      this.items[ind].dataset.hovered = true;
      this.codeItems[ind].classList.add('hovered');
      this.showText(this.items[ind]);
    }
  },

  hideTableTooltip(e): void {
    e.stopPropagation()
    if (e.target.classList.contains('items')) return
    const ind = this.items.indexOf(e.target)
    this.items[ind].dataset.hovered = false;
    this.codeItems[ind].classList.remove('hovered');
    this.helper.style.display = 'none';
  },

  hideEditor(e): void {
    e.stopPropagation()
    if (e.target.classList.contains('markup__viewer')) return
    const ind = this.codeItems.indexOf(e.target.closest('div'))
    if (ind !== -1) {
      this.items[ind].dataset.hovered = false;
      this.codeItems[ind].classList.remove('hovered');
      this.helper.style.display = 'none';
    }
  },
}
