import hljs from "highlight.js/lib/core"
import html from "highlight.js/lib/languages/xml"
import helping from './helping'
import table from './table'
import { levels} from './levels'
import levelMenu from './levelMenu'
import htmlCode from './htmlCode'
import helpTooltip from './helpTooltip'

hljs.registerLanguage("HTML", html);

export default {
  currentLevel: 1,
  usersNodeList: [],
  winNodeList: [],
  levelTimeout: 1000,
  winSelector: '',
  input: document.querySelector('.css-editor__input'),

  init() :void {
    this.getCurrentLevel();
    table.renderItemsToTable(levels[this.currentLevel - 1].items);
    table.renderTaskDescription(levels[this.currentLevel - 1].title);
    levelMenu.init();
    levelMenu.setCurrentLevel(this.currentLevel);
    htmlCode.renderMarkup();
    this.addStartAnimation();
    this.addEvents();
    this.setColorToCod();
    helpTooltip.getItems();
  },

  setColorToCod() {
    hljs.configure({ useBR: true });
    document.querySelectorAll('.code').forEach((block: HTMLElement) => {
      hljs.highlightBlock(block);
    });
  },

  getCurrentLevel(): void {
    const curLevel = localStorage.getItem('currentLevel')
    if(localStorage.getItem('currentLevel')) {
      this.currentLevel = curLevel
    }
  },

  setCurrentLevel(): void {
    localStorage.setItem('currentLevel', `${this.currentLevel}`)
  },

  changeCurrentLevel(event): void {
    const currentLevel = event.target.closest('a').querySelector('.menu-level__num').innerText;
    this.currentLevel = currentLevel;
    this.changeLevel()   
    levelMenu.toggleMenu() 
  },

  changeLevel() {
    this.setCurrentLevel();
    this.input.value = '';
    levelMenu.setCurrentLevel(this.currentLevel);
    table.renderItemsToTable(levels[this.currentLevel - 1].items);
    this.addStartAnimation();
    table.renderTaskDescription(levels[this.currentLevel - 1].title);
    htmlCode.renderMarkup();
    this.setColorToCod();
    helpTooltip.getItems();
  },

  getUsersNodeList(event): void {
    if((event.type === 'keydown' && event.keyCode === 13) || event.type === 'click') {
      const button: HTMLElement = document.querySelector('.css-editor__button');
      const selector: string = this.input.value;
      button.classList.add('enterhit')
      button.onanimationend = () => button.classList.remove('enterhit');
      try {
        const tableElem: HTMLElement = document.querySelector('.table__surface');
        this.usersNodeList = tableElem.querySelectorAll(selector);
        this.checkWinState()
      } catch(err) {
        console.log(err)
        table.addFalseAnimation()
      }
    }
  },


  getWinNodeList(): void {
    const { winSelector } = levels[this.currentLevel - 1];
    const items: HTMLElement = document.querySelector('.items');
    this.winSelector = winSelector;
    this.winNodeList = items.querySelectorAll(this.winSelector);
  },

  addStartAnimation(): void {
    this.getWinNodeList();
    table.toggleWinsItemsAnimation(this.winNodeList);
  },

  changeNextLevel(): void {
    if (this.currentLevel == levels.length) return
    this.currentLevel = +this.currentLevel + 1;
    this.changeLevel();
  },

  winGame(help){
    levelMenu.setDoneLevels(+this.currentLevel, help);
    levelMenu.addDoneStyle();
    alert('you are the best, man')
    this.currentLevel = 1;
    setTimeout(() => this.changeLevel(), this.levelTimeout)
  },

  checkWinGame(help: boolean): void {
    if (this.currentLevel == levels.length) {
      this.winGame(help)
    } else {
      this.winLevel(help)
      setTimeout(() => this.changeNextLevel(), this.levelTimeout)
    }
  },

  winLevel(help: boolean): void {
    table.toggleWinsItemsAnimation(this.winNodeList);
    table.addWinAnimation(this.winNodeList);
    levelMenu.setDoneLevels(+this.currentLevel, help);
    levelMenu.addDoneStyle();
  },

  checkWinState(): void {
    const items: HTMLElement = document.querySelector('.items');
    const editor: HTMLElement = document.querySelector('.editor');
    let isWin = true;

    if (this.usersNodeList.length === 0) {
      isWin = false;
      table.addFalseAnimation()
    }

    this.usersNodeList.forEach((e: HTMLElement, i: number) => {
      if(e !== this.winNodeList[i]) {
        this.usersNodeList.forEach((elem: HTMLElement) => {
          isWin = false;
          elem.classList.add('shake');
          // eslint-disable-next-line no-param-reassign
          elem.onanimationend = () => e.classList.remove('shake');
        });
      }
    });
    if (isWin) {
      this.checkWinGame()
    }
  },

  getHelp(): void {
    const TABLE_ANIMATION_DELAY = 1000;
    const delay = 300;
    const helpButton: HTMLButtonElement = document.querySelector('#help-button');
    helpButton.disabled = true;
    helping.help(this.winSelector, this.input, delay);
    setTimeout(() => this.checkWinGame(true), this.winSelector.length * delay)
    // eslint-disable-next-line max-len
    setTimeout(() => { helpButton.disabled = false }, this.winSelector.length * delay + TABLE_ANIMATION_DELAY)
  },

  resetProgress(): void {
    levelMenu.resetProgress();
    this.currentLevel = 1;
    this.changeLevel();
    levelMenu.toggleMenu()
  },

  addEvents(): void {
    document.querySelector('.menu-level__levels').addEventListener('click', (e) => this.changeCurrentLevel(e));
    document.querySelector('.css-editor__button').addEventListener('click', (e) => this.getUsersNodeList(e))
    document.addEventListener('keydown', (e) => this.getUsersNodeList(e))
    document.querySelector('#help-button').addEventListener('click', () => this.getHelp())
    document.querySelector('#reset-button').addEventListener('click', () => this.resetProgress())
    document.querySelector('.menu-button').addEventListener('click', () => levelMenu.toggleMenu())
    document.addEventListener('mouseover', (e) => helpTooltip.showTooltip(e))
    document.addEventListener('mouseout', (e) => helpTooltip.hideTooltip(e))
  }
}