export default {
  renderItemsToTable(items: string): void {
    const table: HTMLElement = document.querySelector('.items');
    table.innerHTML = items;
    this.setTableWidth()
  },

  renderTaskDescription(title: string): void {
    const taskDescription = document.querySelector('.task-description');
    taskDescription.innerHTML = title;
  },

  toggleWinsItemsAnimation(list: NodeList): void {
    list.forEach((e:HTMLElement) => {
      e.classList.toggle('strobe')
    })
  },

  addWinAnimation(list: NodeList): void {
    list.forEach((e:HTMLElement) => {
      e.classList.add('win')
    })
  },

  addFalseAnimation(): void {
    const editor:HTMLElement = document.querySelector('.editor');
    editor.classList.add('shake');
    editor.onanimationend = () => editor.classList.remove('shake');
  },

  setTableWidth(): void {
    const tableSurface: HTMLElement = document.querySelector('.table__surface');
    const tableEdge: HTMLElement = document.querySelector('.table__edge');
    const { width } = getComputedStyle(tableSurface);
    tableEdge.style.width = width;
  },
}
